import { CY } from "../js/aisdk.js";
const crypto = require("crypto");
let licenceKey = null;
let facex = null;
let enxRoomObj = null;
let stopEnxFace = {};
let facexUID = null;
("use strict");
export default class EnxFaceX {
  constructor() {
    this.CY = CY;
  }

  init(roomParams, stream = null, callback) {
    var decipher = crypto.createDecipher(
      "aes-256-cbc",
      roomParams.roomData.conf_num.toString()
    );
    var dec = decipher.update(roomParams.roomData.analyzer, "hex", "utf8");
    dec += decipher.final("utf8");
    licenceKey = dec;
    enxRoomObj = roomParams.room;
    if (enxRoomObj && licenceKey) {
      enxRoomObj.pingBack((data) => {
        if (data.result === 0) {
          facexUID = Date.now();
          if (stream) {
            const facexCanvas = document.createElement("canvas");
            facexCanvas.setAttribute("id", "facexCanvas");
            facexCanvas.style.display = "none";
            const streamSource = {
              getFrame(maxSize) {
                return new Promise((resolve) => {
                  resolve(stream.getVideoFrameImage("facexCanvas"));
                });
              },
              start() {},
              stop() {},
              get stopped() {},
            };
            facex = this.CY.loader()
              .licenseKey(licenceKey)
              .source(streamSource);
          } else {
            facex = this.CY.loader().licenseKey(licenceKey);
          }
        }
        callback(data);
      });
    }
  }
  /*Function enableFace 
   This function return the following arrays
   faces: An array containing the detected faces in form of ImageData objects (zero or one; or multiple faces, if fullFrameDetection is true)
   rects: An array of objects describing the bounding boxes (zero or one; or multiple rects, if fullFrameDetection is true)
   x: The upper left point x coordinate
   y: The upper left point y coordinate
   width:The width of the bounding box
   height:The height of the bounding box
   status:A string containing the status of the face tracker
   "INIT": Tracker is initializing; zero or many faces could be returned
   "TRACK_OK": Tracker is correctly tracking one face; one face is returned
   "RECOVERING": Tracker has lost a face and is attempting to recover and continue tracking; zero faces are returned
   fullFrameDetection: A boolean. It is true when detection was full-frame and multiple faces can be returned, false otherwise.*/
  startFaceDetector(config = {}, callback) {
    const data = {
      idx: `FACE_DETECTOR-${facexUID}`,
      tool: "facex",
      method: "FACE_DETECTOR",
      stop_after: null,
    };
    enxRoomObj.startClientUsage(data, (data) => {
      if (data.result === 0) {
        facex
          .addModule(this.CY.modules().FACE_DETECTOR.name, config)
          .load()
          .then(({ start, stop }) => {
            stopEnxFace.FACE_DETECTOR = stop;
            start();
            callback(data);
          });

        window.addEventListener(
          this.CY.modules().FACE_DETECTOR.eventName,
          (evt) => {
            let faceEvt = new CustomEvent("facex-detector", {
              detail: evt.detail,
            });
            window.dispatchEvent(faceEvt);
          }
        );
      }
    });
  }
  stopFaceDetecTor(callback) {
    this.stopFaceEvt("FACE_DETECTOR", (res) => {
      callback(res);
    });
  }
  /* Function enableFacePose
   This function return the output of pose prediction
   An object containing the output of the pose prediction
   pose: An object containing the filtered (smoothened) pose rotation angles expressed in radiants as pitch, roll and yaw.*/
  startFacePose(config = {}, callback) {
    const data = {
      idx: `FACE_POSE-${facexUID}`,
      tool: "facex",
      method: "FACE_POSE",
      stop_after: null,
    };
    enxRoomObj.startClientUsage(data, (data) => {
      if (data.result === 0) {
        facex
          .addModule(this.CY.modules().FACE_POSE.name, config)
          .load()
          .then(({ start, stop }) => {
            stopEnxFace.FACE_POSE = stop;
            start();
            callback(data);
          });

        window.addEventListener(
          this.CY.modules().FACE_POSE.eventName,
          (evt) => {
            let facePoseEvt = new CustomEvent("facex-pose", {
              detail: evt.detail,
            });
            window.dispatchEvent(facePoseEvt);
          }
        );
      }
    });
  }
  stopFacePose(callback) {
    this.stopFaceEvt("FACE_POSE", (res) => {
      callback(res);
    });
  }
  /*Function enableFaceAge 
   It returns an object containing the output of the age prediction
   age: An object containing the probabilities of the filtered (smoothened) age prediction:
   _-18: The probability that predicted age is less than 18 years old.
   18-35: The probability that predicted age is greater than or equal to 18 years old and less than 35 years old.
   35-51: The probability that predicted age is greater than or equal to 35 years old and less than 51 years old.
   51-_: The probability that predicted age is greater than or equal to 51 years old.
   numericAge: A numeric estimate for the age
   Note: in case of poor quality of the prediction, the event is not fired (i.e. skipped for that frame).*/
  startFaceAge(config = {}, callback) {
    const data = {
      idx: `FACE_AGE-${facexUID}`,
      tool: "facex",
      method: "FACE_AGE",
      stop_after: null,
    };
    enxRoomObj.startClientUsage(data, (data) => {
      if (data.result === 0) {
        facex
          .addModule(this.CY.modules().FACE_AGE.name, config)
          .load()
          .then(({ start, stop }) => {
            stopEnxFace.FACE_AGE = stop;
            start();
            callback(data);
          });

        window.addEventListener(this.CY.modules().FACE_AGE.eventName, (evt) => {
          let faceAgeEvt = new CustomEvent("facex-age", {
            detail: evt.detail,
          });
          window.dispatchEvent(faceAgeEvt);
        });
      }
    });
  }
  stopFaceAge(callback) {
    this.stopFaceEvt("FACE_AGE", (res) => {
      callback(res);
    });
  }
  /*Function enableFaceEmotion
   output: An object containing the output of the emotion prediction
   dominantEmotion: the name of the dominant emotion if present, otherwise it is undefined.
   emotion: An object containing the filtered (smoothened) values of the probability distribution of emotions.
   The sum of all the probabilities is always 1, each probability in the distribution has a value between 0 and 1.
   Angry: The probability for Angry.
   Disgust: The probability for Disgust.
   Fear: The probability for Fear.
   Happy: The probability for Happy.
   Sad: The probability for Sad.
   Surprise: The probability for Surprise.
   Neutral: The probability for Neutral.*/
  startFaceEmotion(config = {}, callback) {
    const data = {
      idx: `FACE_EMOTION-${facexUID}`,
      tool: "facex",
      method: "FACE_EMOTION",
      stop_after: null,
    };
    enxRoomObj.startClientUsage(data, (data) => {
      if (data.result === 0) {
        facex
          .addModule(this.CY.modules().FACE_EMOTION.name, config)
          .load()
          .then(({ start, stop }) => {
            stopEnxFace.FACE_EMOTION = stop;
            start();
            callback(data);
          });

        window.addEventListener(
          this.CY.modules().FACE_EMOTION.eventName,
          (evt) => {
            let faceEmotionEvt = new CustomEvent("facex-emotion", {
              detail: evt.detail,
            });
            window.dispatchEvent(faceEmotionEvt);
          }
        );
      }
    });
  }
  stopFaceEmotion(callback) {
    this.stopFaceEvt("FACE_EMOTION", (res) => {
      callback(res);
    });
  }
  /*Function enableFaceGender
   output: An object containing the output of the gender prediction
   gender: An object containing the filtered (smoothened) probabilities of the gender prediction:
   Female: The probability that predicted gender is Female.
   Male: The probability that predicted gender is Male.
   mostConfident: Gender name of the most likely result if its smoothened probability is above the threshold, otherwise it is undefined.*/
  startFaceGender(config = {}, callback) {
    const data = {
      idx: `FACE_GENDER-${facexUID}`,
      tool: "facex",
      method: "FACE_GENDER",
      stop_after: null,
    };
    enxRoomObj.startClientUsage(data, (data) => {
      if (data.result === 0) {
        facex
          .addModule(this.CY.modules().FACE_GENDER.name, config)
          .load()
          .then(({ start, stop }) => {
            stopEnxFace.FACE_GENDER = stop;
            start();
            callback(data);
          });

        window.addEventListener(
          this.CY.modules().FACE_GENDER.eventName,
          (evt) => {
            let faceGenderEvt = new CustomEvent("facex-gender", {
              detail: evt.detail,
            });
            window.dispatchEvent(faceGenderEvt);
          }
        );
      }
    });
  }
  stopFaceGender(callback) {
    this.stopFaceEvt("FACE_GENDER", (res) => {
      callback(res);
    });
  }
  /*Function enableFaceFeatures
   output: An object containing the output of the face features prediction
   features: An object containing the filtered (smoothened) probabilities of each face independent feature in range [0.0, 1.0]:
   Arched Eyebrows
   Attractive
   Bags Under Eyes
   Bald
   Bangs
   Beard 5 O'Clock Shadow
   Big Lips
   Big Nose
   Black Hair
   Blond Hair
   Brown Hair
   Chubby
   Double Chin
   Earrings
   Eyebrows Bushy
   Eyeglasses
   Goatee
   Gray Hair
   Hat
   Heavy Makeup
   High Cheekbones
   Lipstick
   Mouth Slightly Open
   Mustache
   Narrow Eyes
   Necklace
   Necktie
   No Beard
   Oval Face
   Pale Skin
   Pointy Nose
   Receding Hairline
   Rosy Cheeks
   Sideburns
   Straight Hair
   Wavy Hair*/
  startFaceFeatures(config = {}, callback) {
    const data = {
      idx: `FACE_FEATURES-${facexUID}`,
      tool: "facex",
      method: "FACE_FEATURES",
      stop_after: null,
    };
    enxRoomObj.startClientUsage(data, (data) => {
      if (data.result === 0) {
        facex
          .addModule(this.CY.modules().FACE_FEATURES.name, config)
          .load()
          .then(({ start, stop }) => {
            stopEnxFace.FACE_FEATURES = stop;
            start();
            callback(data);
          });

        window.addEventListener(
          this.CY.modules().FACE_FEATURES.eventName,
          (evt) => {
            let faceFeaturesEvt = new CustomEvent("facex-features", {
              detail: evt.detail,
            });
            window.dispatchEvent(faceFeaturesEvt);
          }
        );
      }
    });
  }
  stopFaceFeatures(callback) {
    this.stopFaceEvt("FACE_FEATURES", (res) => {
      callback(res);
    });
  }
  /*Function enableFaceArousalValence
   output: An object containing the output of the face arousal/valence prediction
   arousalvalence: An object containing the filtered (smoothened) values.
   arousal: value in range [-1.0, 1.0]. It represents the degree of engagement (positive arousal), or disengagement (negative arousal).
   valence: value in range [-1.0, 1.0]. It represents the degree of pleasantness (positive valence), or unpleasantness (negative valence).*/
  startFaceArousalValence(config = {}, callback) {
    const data = {
      idx: `FACE_AROUSAL_VALENCE-${facexUID}`,
      tool: "facex",
      method: "FACE_AROUSAL_VALENCE",
      stop_after: null,
    };
    enxRoomObj.startClientUsage(data, (data) => {
      if (data.result === 0) {
        facex
          .addModule(this.CY.modules().FACE_AROUSAL_VALENCE.name, config)
          .load()
          .then(({ start, stop }) => {
            stopEnxFace.FACE_AROUSAL_VALENCE = stop;
            start();
            callback(data);
          });

        window.addEventListener(
          this.CY.modules().FACE_AROUSAL_VALENCE.eventName,
          (evt) => {
            let faceArousalValenceEvt = new CustomEvent(
              "facex-arousal-valence",
              {
                detail: evt.detail,
              }
            );
            window.dispatchEvent(faceArousalValenceEvt);
          }
        );
      }
    });
  }
  stopFaceArousalValence(callback) {
    this.stopFaceEvt("FACE_AROUSAL_VALENCE", (res) => {
      callback(res);
    });
  }
  /*Function enableFaceAttention
   output: An object containing the output of the face attention prediction
   attention: filtered value (smoothened) in range [0.0, 1.0]. A value close to 1.0 represents attention, a value close to 0.0 represents distraction.*/
  startFaceAttention(config = {}, callback) {
    const data = {
      idx: `FACE_ATTENTION-${facexUID}`,
      tool: "facex",
      method: "FACE_ATTENTION",
      stop_after: null,
    };
    enxRoomObj.startClientUsage(data, (data) => {
      if (data.result === 0) {
        facex
          .addModule(this.CY.modules().FACE_ATTENTION.name, config)
          .load()
          .then(({ start, stop }) => {
            stopEnxFace.FACE_ATTENTION = stop;
            start();
            callback(data);
          });

        window.addEventListener(
          this.CY.modules().FACE_ATTENTION.eventName,
          (evt) => {
            let facexAttentionEvt = new CustomEvent("facex-attention", {
              detail: evt.detail,
            });
            window.dispatchEvent(facexAttentionEvt);
          }
        );
      }
    });
  }
  stopFaceAttention(callback) {
    this.stopFaceEvt("FACE_ATTENTION", (res) => {
      callback(res);
    });
  }

  stopFaceEvt(name, callback) {
    if (stopEnxFace[name]) {
      const data = { idx: `${name}-${facexUID}`, tool: "facex", method: name };
      enxRoomObj.stopClientUsage(data, (data) => {
        if (data.result === 0) {
          setTimeout(stopEnxFace[name], 10);
          callback(data);
        }
      });
    } else {
      callback({ result: 1, message: `${name} not yet Started` });
    }
  }
}
